/* animation */
.taiji {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#000000),
    color-stop(50%, #000000),
    color-stop(50%, #000000),
    color-stop(50%, #ffffff),
    to(#ffffff)
  );
  background: linear-gradient(
    to bottom,
    #000000 0%,
    #000000 50%,
    #000000 50%,
    #ffffff 50%,
    #ffffff 100%
  );
  position: relative;
  -webkit-animation: rotate1 1s linear infinite;
          animation: rotate1 1s linear infinite;
  margin: 0 auto;
}
.taiji > div:nth-child(0n + 1) {
  background: black;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.taiji > div:nth-child(0n + 1) div {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background: white;
}
.taiji > div:nth-child(0n + 2) {
  background: white;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.taiji > div:nth-child(0n + 2) div {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background: black;
}
@-webkit-keyframes rotate1 {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
}
}
@keyframes rotate1 {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
}
}

