  font-size: 13px;
  text-decoration: none;
}
#reply a {
  color: #08c;
}
#reply span {
  color: #666;
}
.replySec {
  border-bottom: 1px solid #e5e5e5;
  padding: 0 10px;
}
.loading {
  text-align: center;
  padding-top: 300px;
}
.replyUp a:nth-of-type(2) {
  margin-left: 0px;
  display: inline-block;
}
.reply_content {
  margin-top: 10px;
  padding-left: 40px;
  font-size: 15px;
}
.reply_content .markdown-text img{
  width: 15%;
  height: auto;
}
.topic_header {
  padding: 10px;
}
.topic_title {
  font-size: 20px;
  font-weight: bold;
  padding-top: 8px;
}
.topic_header ul {
  list-style: none;
  padding: 0px 0px;
  margin: 6px 0px;
}
.topic_header li {
  display: inline-block;
  font-size: 12px;
  color: #838383;
}
.topic_content {
  border-top: 1px solid #e5e5e5;
  padding: 0 10px;
}
.topic_content p {
  font-size: 15px;
  line-height: 1.7em;
  overflow: auto;
  margin: 1em 0;
}
.markdown-text img {
  width: 92% ;
}

